import React, { Component } from 'react';
import './App.css';
import Listings from './Listings';
import swal from "sweetalert";
import axios from "axios/index";
import {Redirect } from "react-router-dom";

class Verify extends Component {
    constructor(props) {
        super(props);
        this.state = {
            email: 'Email'

        };
    }

    sendData() {
        if (!/^[A-Z0-9._%+-]+@zagmail.gonzaga.edu$/i.test(this.state.email)) {
            swal("Only Gonzaga Emails Allowed, Sorry!" , {
                buttons: {
                    return: {
                        text: "Only Gonzaga Emails Allowed, Sorry!",
                        value: "listing",
                    }
                }

            });
            return;
        } else {
            axios.post('https://intense-eyrie-26400.herokuapp.com/ip', {
                email: this.state.email
            });

            swal("Congrats Check your Email!" , {
                buttons: {
                    return: {
                        text: "Make sure to check your Junk Folder!",
                        value: "listing",
                    }
                }

            });
        }

    }

    render() {

        return (
            <div>
                <h1 className= 'text-center'> Verify Your Email</h1>
                <form onSubmit={ this.onSubmit }>
                    <div className= 'container'>
                        <div className= 'form-group'>
                            <label >Email</label>
                            <input type="email" className="form-control" value = {this.state.email}  onChange={ e => this.setState({ email : e.target.value }) } id="email" placeholder="name@example.com"></input>
                        </div>

                        <div>
                            <button type="button" className="btn btn-primary" onClick = {this.sendData.bind(this)}>Submit</button>
                        </div>

                    </div>

                </form>


            </div>

        );
    }
}

export default Verify;


