import React, { Component } from 'react';
import './App.css';
import axios from 'axios';
import swal from "sweetalert";
import {Redirect } from "react-router-dom";
import football from './images/football.jpeg';
import CommentBoard from './CommentBoard';

class Listing extends Component {
    constructor(props) {
        super(props);
        this.state = {
            listing: {}
        };
    }

    componentDidMount() {
        axios.get('http://localhost:3001/isIpVerified')
            .then(res => {
                if (res.data == '') { //no ip found
                    swal("Your Ip address was not recognized" ,{buttons: {
                            return: {
                                text: "Lets check it out!",
                                value: "listing",
                            }
                        }
                    }).then((value) => {
                        window.location.href = "/Verify"
                        return <Redirect to="/Verify" />
                    });

                } else {
                    axios.get(`http://localhost:3001/listings/${this.props.match.params.id}`)
                        .then(res => {
                            this.setState({ listing: res.data })

                        })
                        .catch(err => console.log("some err occured", err))
                }

            })
            .catch(err => console.log("some err occured", err))


    }

    render() {

        return (
            <div className= 'container'>
                <div style = {{marginTop:'20px'}} className="card mb-3 ">
                    <img className="card-img-top" style = {{height:'200px'}} src={football} alt="Card image cap"></img>
                    <div className="card-body">
                        <h4 className="card-title">{this.state.listing.title}</h4>
                        <p className="card-text">{this.state.listing.description}</p>
                        <p className="card-text">
                            <small class="text-muted">{this.state.listing.game + ' @ ' + this.state.listing.location}</small>
                        </p>
                    </div>
                </div>
                <div>
                    <CommentBoard pid = {this.props.match.params.id}/>
                </div>
            </div>

        );
    }
}

export default Listing;


