import React, { Component } from 'react';
import './App.css';
import axios from 'axios';
import swal from "sweetalert";
import {Redirect } from "react-router-dom";
import football from './images/football.jpeg';


class Listing extends Component {
    constructor(props) {
        super(props);
        this.state = {
            comments: [],
            addedComment: 'n/a',
            currentUserEmail: 'usermakingcomment@testickles.com'
        }
    }



    sendData() {
        console.log(this.state.currentUserEmail);
        axios.post('https://intense-eyrie-26400.herokuapp.com/comment', {
            comment: this.state.addedComment,
            email: this.state.currentUserEmail,
            pid: this.props.pid
        });

        swal("Congrats Your Comment was Posted!" ,{buttons: {
                return: {
                    text: "Check it out!"
                }
            }
        }).then(() => {
            this.componentDidMount();
        });
    }

    componentDidMount() {
        const _this = this;
        axios.get(`https://intense-eyrie-26400.herokuapp.com/comment/${this.props.pid}`)
            .then(function(response) {
                _this.setState({
                    comments: response.data
                });

            })
            .catch(function(response) {
                console.log(response);
            });
        //get current users email in case they make a comment
        axios.get('https://intense-eyrie-26400.herokuapp.com/ip')
            .then(function(response) {
                _this.setState({
                    currentUserEmail: response.data.email
                });

            })
            .catch(function(response) {
                console.log(response);
            });


    }

    render() {

        return (
            <div style={{marginTop:'10px'}} className= 'container'>
                <h3 className= 'text-center'> Comments</h3>
                <div>
                    {
                        this.state.comments.map((item, index) => (
                            <div class="card">
                                <div class="card-header">
                                    {item.email}
                                </div>
                                <div class="card-body">
                                    <p class="card-text">{item.comment}</p>
                                </div>
                            </div>
                        ))
                    }
                </div>
                <form onSubmit={ this.onSubmit }>
                    <div>

                        <textarea type="text" className="form-control" value = {this.state.addedComment}  onChange={ e => this.setState({ addedComment : e.target.value }) } placeholder="I would love to go"></textarea>
                    </div>
                    <div >
                        <button type="button" className="btn btn-primary" onClick = {this.sendData.bind(this)}>Submit</button>
                        <label style = {{marginLeft: '10px'}} className= 'text-left'>Add a Comment</label>
                    </div>
                </form>
            </div>


        );
    }
}

export default Listing;


