import React, { Component } from 'react';
import './App.css';
import axios from "axios";
import {Link} from "react-router-dom";
import swal from "sweetalert";
import {Redirect } from "react-router-dom";
import Soccer from './images/soccer.jpeg';
import Football from './images/football.jpeg';
import Frisbee from './images/frisbee.jpg';
import Baseball from './images/baseball.jpg';
import Basketball from './images/basketball.jpg';
import Running from './images/running.jpg';
import Maps from "./Maps";


class Listings extends Component {
    constructor(props) {
        super(props);
        this.state = {
           data: []
        };
    }


    componentWillReceiveProps(nextProps) {
        this.setState({ data: nextProps.listings });
    }



    render() {
        console.log('PROPS ENTERING')
        console.log(this.state.data);
        let image = [];
        for (var i = 0; i < this.state.data.length; i++) {
            if (this.state.data[i].game === 'Football') {
                image[i] = <img style={{height:'180px'}} class="card-img-top" src={Football} alt="Card image cap"></img>
            }
            if (this.state.data[i].game === 'Soccer') {
                image[i] = <img style={{height:'180px'}} class="card-img-top" src={Soccer} alt="Card image cap"></img>
            }
            if (this.state.data[i].game === 'Baseball') {
                image[i] = <img style={{height:'180px'}} class="card-img-top" src={Baseball} alt="Card image cap"></img>
            }
            if (this.state.data[i].game === 'Running') {
                image[i] = <img style={{height:'180px'}} class="card-img-top" src={Running} alt="Card image cap"></img>
            }
            if (this.state.data[i].game === 'Frisbee') {
                image[i] = <img style={{height:'180px'}} class="card-img-top" src={Frisbee} alt="Card image cap"></img>
            }
            if (this.state.data[i].game === 'Basketball') {
                image[i] = <img style={{height:'180px'}} class="card-img-top" src={Basketball} alt="Card image cap"></img>
            }
        }

        return (
            <div style={{marginTop: '50px'}}>

                <div className= 'row'>
                    <div className="col-sm-7">
                        {
                                this.state.data.map((item, index) => (
                                        <div class="card">
                                            {image[index]}
                                            <div class="card-body">
                                                <h4 class="card-title">{item.title}</h4>
                                                <p class="card-text">{item.game}</p>
                                                <a href={`Listing/${item._id}`}  class="btn btn-primary">Check it Out!</a>
                                            </div>
                                        </div>



                                ))

                        }
                    </div>
                    <div className= 'col-sm-5'>
                        <Maps/>
                    </div>

                </div>

            </div>
        );
    }
}

export default Listings;


