import React, { Component } from 'react';
import './App.css';
import swal from "sweetalert";
import axios from "axios/index";
import {Redirect } from "react-router-dom";
import evan from './images/evan.jpg';


class FAQ extends Component {
    constructor(props) {
        super(props);
        this.state = {

        };
    }

    componentDidMount() {
        axios.get('https://intense-eyrie-26400.herokuapp.com/isIpVerified')
            .then(res => {
                if (res.data == '') { //no ip found
                    swal("Your Ip address was not recognized" ,{buttons: {
                            return: {
                                text: "Lets check it out!",
                                value: "listing",
                            }
                        }
                    }).then((value) => {
                        window.location.href = "/Verify"
                        return <Redirect to="/Verify" />
                    });

                }

            })
            .catch(err => console.log("some err occured", err))
    }
    render() {

        return (
            <div style = {{marginTop: '50px'}} className= 'container  border-dark'>
                <h1 className= 'text-center'> About The Author</h1>
                <div className= 'row'>
                    <div className='col-sm-6'>
                        <li className= 'text-left'>Hi My name is Evan.</li>
                        <li className= 'text-left'>This project started out as a simple data base project.</li>
                        <li className= 'text-left'>React is an excellent Web Development Framework.</li>
                        <li className= 'text-left'>I work as a consulatant at Avanade.</li>
                        <li className= 'text-left'> Check out my {" "}
                            <a href="https://github.com/earends">Github</a>
                        </li>
                        <li className= 'text-left'> Check out my 2018 SeniorDesign website{" "}
                            <a href="https://usesparespace.com">spare space</a>
                        </li>
                        <li className= 'text-left'>This project started out as a simple data base project.</li>

                    </div>
                    <div className='col-sm-6'>
                        <img style={{height:'400px'}} className= 'rounded-circle float-right' src = {evan}></img>
                    </div>

                </div>


            </div>
        );
    }
}

export default FAQ;


